import React from 'react'
import ReactDOM from 'react-dom'
import PopUpContainer from './popUpContainer.jsx'

function popUp(data) {

  ReactDOM.render(
      <PopUpContainer link={data.link} title={data.title} time={data.time} artist={data.artist} close={close}  />,
      document.querySelector('.popup')
    );
}

function close() {
  ReactDOM.unmountComponentAtNode(document.querySelector('.popup'));
}

export default popUp
