import React from 'react'
import axios from 'axios'

import Loading from './loading.jsx'
import Close from './close.jsx'

export default class PopUpContainer extends React.Component {
    constructor(props) {
        super(props);
        this.state = { 
            loading: true,
            content: '',
            seccontent: ''
        }
      }

    componentDidMount() {
        axios.get(this.props.link)
          .then(res => {
            let parser = new DOMParser()
            let doc = parser.parseFromString(res.data, "text/html")
            let content = doc.querySelector('.timetable-first-content')
            let seccontent = doc.querySelector('.timetable-second-content')
            this.setState({ 
                loading: false,
                content: content ? content.innerHTML : '',
                seccontent: seccontent ? seccontent.innerHTML : ''
            })
          })
          .catch(function (error) {
            console.log(error);
          })
      }


    render () {
        return (
            <div className="popup-container">
                <div className="popup-inner-container">
                    <section className="popup-inner-header">
                        <div className="popup-inner-header-title">
                            <div className="popup-inner-header-title-time date">{this.props.time}</div>
                            <h2 className="capitalize">{this.props.artist}</h2>
                            <h2 dangerouslySetInnerHTML={{__html: this.props.title }} />
                        </div>
                        <div className="popup-inner-header-close">
                            <button className="popup-close" onClick={this.props.close}>
                                <Close />
                                <div className="small">Close</div>
                            </button>
                        </div>
                    </section>
                    <section className="popup-inner-content">
                        <Loading loading={this.state.loading} />
                        <div dangerouslySetInnerHTML={{__html: this.state.content }} />
                        <div dangerouslySetInnerHTML={{__html: this.state.seccontent }} />
                    </section>
                    <a className="popup-inner-more" href={this.props.link}>Read more</a>
                </div>
            </div>
        )
    }
}

